@use "../../assets/sass/lib/funcs";

.nav-item {
  margin: 0.5rem;
  border-radius: 0.5rem;
  &:hover {
    background-color: funcs.palette(primary-light);
    color: funcs.palette(bg);
  }
}

.nav-link {
  padding: 0 0.375rem 0 0.375rem;
  border: none;
  color: inherit;
}

.active {
  background-color: funcs.palette(primary-light);
  color: funcs.palette(bg);
}
